.features {
    position: relative;

    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 1347px;
        top: -30px;
        background-image: url("../img/features_background.png");
        background-repeat: no-repeat;
        z-index: -1;

        @media (max-width: 1023px) {
            top: 0;
            background-position: -350px -85px;
        }

        @media (max-width: 767px) {
            top: 0;
            background-position: -680px 477px;
        }
    }
}

.features__wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
    padding-top: 109px;
    box-sizing: border-box;
    color: $dark-grey;

    @media (max-width: 1023px) {
        padding: 54px 24px 0;
    }

    @media (max-width: 767px) {
        padding: 37px 16px 0 14px;
    }
}

.features__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 0;
    padding: 0;
    list-style: none;
}

.features__item {
    width: 570px;
    min-height: 299px;
    margin-bottom: 30px;
    padding: 40px 42px 20px;
    box-sizing: border-box;
    background-color: $white;
    border: 1px solid $border-grey;
    border-left: 3px solid $blue;

    @media (max-width: 1200px) {
        width: calc((100% - 30px) / 2);
    }

    @media (max-width: 1023px) {
        min-height: 0;
        padding: 24px 20px 0 24px;

        &:nth-child(2n-1) {
            margin-right: 30px;
        }

        &:last-child {
            padding-bottom: 15px;
        }
    }

    @media (max-width: 767px) {
        width: 100%;
        margin-bottom: 20px;
        padding-right: 14px;

        &:nth-child(2n-1) {
            margin-right: 0;
        }

        &:nth-child(3n-1) {
            padding-right: 10px;
            padding-bottom: 6px;
        }

        &:nth-child(3n) {
            padding-bottom: 35px;
        }

        &:last-child {
            padding-bottom: 0;
        }
    }

    &:hover {
        background-color: $blue;
    }
}

.features__item:hover h3,
.features__item:hover p {
    color: $white;
}

.features__item:hover svg {
    fill: $white;
}

.features__item h3 {
    margin: 0;
    margin-bottom: 15px;
    padding-top: 17px;
    font-weight: normal;
    font-size: 24px;
    line-height: 34px;
    color: $blue;

    @media (max-width: 1023px) {
        margin-bottom: 10px;
        padding-top: 0;
    }

    @media (max-width: 767px) {
        padding-top: 6px;
        font-size: 20px;
        line-height: 28px;
    }
}

.features__item p {
    margin: 0;
    font-family: "PT Mono", "Arial", sans-serif;
    font-size: 16px;
    line-height: 22px;

    @media (max-width: 1023px) {
        margin-bottom: 19px;
    }

    @media (max-width: 767px) {
        margin-bottom: 22px;
    }
}

.features__item:nth-child(3n) h3 {
    @media (max-width: 1023px) {
        margin-bottom: 18px;
    }

    @media (max-width: 767px) {
        padding-top: 0;
    }
}

.features__item:nth-child(3n-1) h3 {
    @media (max-width: 767px) {
        margin-bottom: 5px;
    }
}

.features__item:last-child h3 {
    @media (max-width: 767px) {
        padding-top: 0;
        margin-bottom: 17px;
    }
}