.footer {
    position: relative;
    background-color: $background-white;

    @media (max-width: 1023px) {
        &::after {
            position: absolute;
            content: "";
            width: 100%;
            top: 97px;
            height: 1px;
            background: $background-grey;
            opacity: 0.2;

            @media (max-width: 767px) {
                display: none;
            }
        }
    }
}

.footer__wrapper {
    display: flex;
    max-width: 1420px;
    margin: 0 auto;
    padding-top: 65px;
    padding-bottom: 55px;

    @media (max-width: 1450px) {
        width: 100%;
        padding-left: 20px;
        padding-right: 20px;
        box-sizing: border-box;
    }

    @media (max-width: 1023px) {
        flex-wrap: wrap;
        max-width: 768px;
        padding: 0 25px;
    }

    @media (max-width: 767px) {
        flex-direction: column;
        width: 100%;
        padding: 0;
        margin-bottom: 30px;
    }
}

.footer__block {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;

    @media (max-width: 1023px) {
        width: 100%;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 44px;
        padding-top: 19px;
        padding-bottom: 20px;
    }

    @media (max-width: 767px) {
        flex-direction: column;
        justify-content: center;
        padding-top: 46px;
        padding-bottom: 50px;
        margin-bottom: 0;
    }
}

.footer__block span {
    margin: 0;
    padding-left: 15px;
    font-family: "PT Mono", "Arial", sans-serif;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $background;
    opacity: 0.5;

    @media (min-width: 1023px) {
        display: none;
    }

    @media (max-width: 767px) {
        padding-top: 26px;
        padding-bottom: 20px;
        padding-left: 0;
    }
}

.footer__logo {
    margin-bottom: 48px;
    display: flex;

    @media (max-width: 1023px) {
        margin-bottom: 0;
    }
}

.footer__logo-text {
    margin-top: 6px;
    margin-left: 17px;
}

.footer__logo-text h2 {
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 25px;
    line-height: 29px;
    margin: 0;
    color: $logo-black;
}

.footer__logo p {
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    color: $grey;
    margin: 0;
}

.footer__social-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 210px;
    margin: 0;
    padding: 0;
    list-style: none;
  
    @media (max-width: 767px) {
      margin-top: 12px;
      max-width: 100%;
    }
  }
  
  .footer__social-link svg {
    &:hover {
      fill: $blue;
    }
  
    &:active {
      opacity: 0.5;
    }
  }
  
  .footer__social-item:not(:last-child) {
    margin-right: 16px;
  }

.footer__nav,
.footer__contacts {
    color: $background;

    @media (max-width: 1023px) {
        margin-bottom: 38px;
    }

    @media (max-width: 767px) {
        margin-bottom: 0;
    }
}

.footer__nav {
    position: relative;
    margin-left: auto;
    margin-right: auto;
    padding-left: 132px;

    @media (max-width: 1300px) {
        padding-left: 50px;
    }

    @media (max-width: 1100px) {
        padding-left: 0;
    }

    @media (max-width: 1023px) {
        padding: 0;
        margin-left: 0;
    }

    @media (max-width: 767px) {
        margin-right: 0;
        border-bottom: 1px solid rgba(98, 105, 98, 0.2);
        border-top: 1px solid rgba(98, 105, 98, 0.2);
    }
}

.footer h3 {
    position: relative;
    margin: 0;
    margin-bottom: 25px;
    font-weight: normal;
    font-size: 15px;
    line-height: 16px;
    letter-spacing: 0.05em;
    text-transform: uppercase;

    @media (max-width: 767px) {
        margin-bottom: 0;
        padding: 28px 15px;

        &:hover {
            color: $blue;
        }

        &:active {
            opacity: 0.5;
        }

        &:focus {
            outline: 1px solid $black;
        }
    }
}

.footer__list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: flex-start;
    margin: 0;
    padding: 0;
    list-style: none;

    &--nav {
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: 75px;

        @media (max-width: 1200px) {
            column-gap: 40px;
        }

        @media (max-width: 1100px) {
            column-gap: 20px;
        }

        @media (max-width: 1023px) {
            display: flex;
            flex-direction: column;
        }
    }

    @media (max-width: 1023px) {
        height: auto;
    }

    @media (max-width: 767px) {
        display: none;
    }
}


.footer__item {
    font-family: "PT Mono", "Arial", sans-serif;
    font-size: 15px;
    line-height: 25px;

    &--contacts {
        padding-right: 0;
    }

    @media (max-width: 767px) {
        padding-left: 15px;

        &--contacts {
            &:first-of-type {
                padding-right: 30px;
            }

            &:last-child {
                padding-bottom: 25px;
            }
        }
    }
}

.footer__item--nav {
    @media (max-width: 1023px) {
        &:nth-child(2) {
            order: 3;
        }

        &:nth-child(4) {
            order: 4;
        }

        &:nth-child(6) {
            order: 5;

            @media (max-width: 767px) {
                padding-bottom: 25px;
            }
        }
    }
}

.footer__item a {
    color: $background;
    opacity: 0.5;

    &:hover {
        color: $blue;
        opacity: 1;
    }

    &:active {
        color: $blue;
        opacity: 0.5;
    }
}

.footer__item p {
    margin: 0;
    color: $background;
    opacity: 0.5;
}

.footer__contacts {
    justify-self: flex-end;
    padding-right: 5px;
    padding-left: 25px;

    @media (max-width: 1200px) {
        padding-left: 0;
        width: 200px;
    }

    @media (max-width: 1023px) {
        width: auto;
        padding-right: 10px;
        padding-left: 0;
    }

    @media (max-width: 767px) {
        padding-right: 0;
        border-bottom: 1px solid rgba(98, 105, 98, 0.2);
    }
}

.footer__accordion h3 {
    @media (max-width: 767px) {
        cursor: pointer;

        &:focus {
            opacity: 0.7;
        }

        &::before,
        &::after {
            position: absolute;
            content: "";
            width: 16px;
            height: 1px;
            right: 17px;
            top: 50%;
            background-color: $accordion-grey;
        }

        &::after {
            transform: rotate(90deg);
        }
    }
}

.footer__accordion--nojs h3 {

    &::before,
    &::after {
        display: none;
    }
}

.footer__accordion--active h3 {
    &::after {
        display: none;
    }
}

.footer__accordion--nojs ul,
.footer__accordion--active ul {
    display: flex;
}

.footer__copyright {
    display: flex;
    flex-wrap: wrap;
    width: 1420px;
    margin: 0 auto;
    padding: 14px 0 0;

    @media (max-width: 1450px) {
        width: 100%;
        padding: 14px 20px;
        box-sizing: border-box;
    }

    @media (max-width: 1023px) {
        position: relative;
        max-width: 768px;
        padding: 0 25px;
    }

    @media (max-width: 767px) {
        flex-direction: column;
        align-items: center;
        max-width: 320px;
        padding: 0 15px 5px;
    }
}

.footer__copyright p {
    margin: 0;
    padding-right: 76px;
    padding-bottom: 14px;
    font-family: "PT Mono", "Arial", sans-serif;
    font-size: 13px;
    line-height: 24px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $background;
    opacity: 0.5;

    @media (max-width: 1200px) {
        padding-right: 50px;
    }

    &:last-child {
        padding-right: 0;
    }

    &:nth-child(2) {
        margin-right: 65px;

        @media (max-width: 1200px) {
            margin-right: 0;
        }
    }

    &:nth-child(3n) {
        margin-right: auto;

        @media (max-width: 767px) {
            margin-right: 0;
        }
    }

    @media (max-width: 1023px) {
        padding: 21px 0 24px;
    }

    @media (max-width: 767px) {
        padding: 0 0 21px;
    }
}

@media (max-width: 1023px) {
    .footer__copyright p {
        &:nth-child(-n+2) {
            display: none;
        }
    }
}

.footer__container {
    border-top: 1px solid rgba(98, 105, 98, 0.2);

    @media (max-width: 767px) {
        border-top: none;
    }
}